.single{
  margin:0px 50px 0px 50px;
  display:-ms-flexbox;
  display:flex;
  background-color:#eee;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.head{
  display:-ms-flexbox;
  display:flex;
   -ms-flex-direction: column;
       flex-direction: column;
}
.series-item{
  list-style-type: none;
  margin: auto;
}
.series-item li{
  float: left;
  margin: 5px;
}
.single img{
  -ms-flex-positive: 2;
      flex-grow: 2;
  height: auto;
}
.single p{
  float:left;
  margin:10px;
}
.single div{
  -ms-flex-positive: 2;
      flex-grow: 2;
}
.detail{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background-color: #777;
}
.summary p{
  text-indent: 40px;
  text-align: justify;
}



